.header {
  position: fixed;
  top: 10px;
  right: 0;
  left: 0;
  min-width: 320px;
  z-index: 1;
  background-color: white;
  background-color: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(4px); }
  @media only screen and (max-width: 768px) {
    .header {
      top: 0; } }
