.nav {
  display: flex;
  align-items: center;
  justify-content: center; }
  @media only screen and (max-width: 768px) {
    .nav {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      background-color: white;
      z-index: -1;
      flex-direction: column;
      align-items: stretch;
      max-height: 0;
      opacity: 0;
      transition: all 400ms cubic-bezier(0.47, 0, 0.745, 0.715);
      overflow: hidden;
      box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.4); }
      .nav.open {
        opacity: 1;
        max-height: 300px; }
      .nav::before {
        display: block;
        height: 60px;
        width: 100%;
        content: ""; }
      .nav::after {
        display: block;
        height: 20px;
        width: 100%;
        content: ""; } }
  .nav-item {
    display: block;
    text-align: center;
    cursor: pointer;
    color: black;
    transition: all ease-in-out 400ms; }
    @media only screen and (max-width: 768px) {
      .nav-item {
        text-align: left; } }
    .nav-item span {
      display: inline-block;
      font-size: 18px;
      font-weight: 600;
      padding: 0 40px;
      box-sizing: border-box; }
      @media only screen and (max-width: 960px) {
        .nav-item span {
          padding: 0 20px;
          font-size: 16px;
          font-weight: 400; } }
      @media only screen and (max-width: 768px) {
        .nav-item span {
          font-size: 18px;
          padding: 10px 10px 10px 20px; } }
    .nav-item:hover {
      color: #ef5550; }

